.app {
    text-align: center;
}

.logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .logo {
        animation: spin infinite 20s linear;
    }
}

.header {
    background-color: #101318;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #ecf0f1;
    position: relative;
}

.link {
    color: #61dafb;
}
